			getNumberText (idx) {
				let arr = ['一','二','三','四','五','六','七','八','九','十']
				return arr[idx]
			},
			handleDeviceChange (res) {
				let idx = res.rowInfo.rowIdx
				let row = res.newTableData[idx] 
				
				this.$nextTick(()=>{	
					if(row && row.deviceCount && row.unitPrice){
						let amount = row.deviceCount * row.unitPrice
						this.$set(this.formData.deviceBudgetList[idx],'amount',amount)
						this.getDeviceCount()
					}
				})
			},
			handleTestChange (res) {
				let idx = res.rowInfo.rowIdx
				let row = res.newTableData[idx] 
				
				this.$nextTick(()=>{	
					if(row && row.deviceCount && row.unitPrice){
						let amount = row.deviceCount * row.unitPrice
						this.$set(this.formData.testBudgetList[idx],'amount',amount)
					}
				})
			},
			getDeviceCount () {
				this.formData.deviceBudgetList.forEach((item)=>{
					if(item.deviceCat && item.unitPrice && item.deviceCount){
						this.getDeviceCountRow(item)
					}
				})
			},
			getDeviceCountRow (item) {
				if(Number(item.deviceCat) === 1){
					if(Number(item.unitPrice) > 100000){
						this.deviceCount.moreThanType1.num = Number(this.deviceCount.moreThanType1.num) + Number(item.deviceCount)
						this.deviceCount.moreThanType1.total =Number(this.deviceCount.moreThanType1.total)+ Number(item.amount)
					} else {
						this.deviceCount.lessThanType1.num= Number(this.deviceCount.lessThanType1.num)+ Number(item.deviceCount)
						this.deviceCount.lessThanType1.total=Number(this.deviceCount.lessThanType1.total)+ Number(item.amount)
					}
				} else {
					if(Number(item.unitPrice) > 100000){
						this.deviceCount.moreThanType2.num = Number(this.deviceCount.moreThanType2.num) + Number(item.deviceCount)
						this.deviceCount.moreThanType2.total =Number(this.deviceCount.moreThanType2.total)+ Number(item.amount)
					} else {
						this.deviceCount.lessThanType2.num= Number(this.deviceCount.lessThanType2.num)+ Number(item.deviceCount)
						this.deviceCount.lessThanType2.total=Number(this.deviceCount.lessThanType2.total)+ Number(item.amount)
					}
				}

				this.deviceCount.total.amount = Number(this.deviceCount.total.amount) + Number(item.amount)
				this.deviceCount.total.num = Number(this.deviceCount.total.num) + Number(item.deviceCount)
			},
			// 获取一个年份的预算
			getOneYearBudget (year) {
				let allBudget = []
				this.budgetMenuList.forEach((o)=>{
					allBudget.push({
						serial: o.serial,
						remark: o.remark,
						name: o.name,
						amount: ''
					})
				})

				let item = {
					budgetAmount: 0,
					budgetYear: year || new Date().getFullYear(),
					budgetDetail:[...allBudget],
					serial: this.formData.budgetPreYearList.length					
				}
				this.formData.budgetPreYearList.push(item)
				this.activeYear = item.budgetYear
				this.yearOptions.push(item.budgetYear)

				allBudget = ''
			},
