	  				this.formData.attachmentList = []
	  			}
	  			this.formData.attachmentList.push({
	  				id: data.id,
	  				projectId: this.formData.id,
	  				fileType:  data.fileExt,
	  				attachmentName: data.fileName,
	  				fileUrl: data.fileUrl,
	  				attachmentRemark: ''
	  			})
	  		},
	  		// 审核意见文件上传
	  		handleSignUploadSuccess(res){
	  			let idx = res.key
	  			let data = res.response.data
	  			let item = {
	  				id: data.id,
	  				projectId: this.formData.id,
	  				fileType:  data.fileExt,
	  				attachmentName: data.fileName,
	  				fileUrl: data.fileUrl,
	  				attachmentRemark: '',
	  				attachmentCategory: '01'
	  			}

	  			switch(idx){
	  				case 0:
	  					item.attachmentCategory = 'attachmentCommittee'
	  					this.formData.attachmentCommittee = {...item}
	  					break
	  				case 1:
	  					item.attachmentCategory = 'attachmentAudit'
	  					this.formData.attachmentAudit = {...item}
	  					break
	  				case 2:
	  					item.attachmentCategory = 'attachmentStatement'
	  					this.formData.attachmentStatement = {...item}
	  					break
	  			}
	  		},
	  		// 下载审核意见模板
			signModelDownload (idx) {
				console.log(idx)
			},
			// 审核意见预览
			handleSignPreview (idx) {
				switch(idx){
					case 0:
						if (!this.formData.attachmentCommittee) {
							this.$dt.showMessage('您还未上传附件','warning')
							return
						}
						window.open(this.formData.attachmentCommittee.fileUrl, "_blank")
						break
					case 1:
						if (!this.formData.attachmentAudit) {
							this.$dt.showMessage('您还未上传附件','warning')
							return
						}
						window.open(this.formData.attachmentAudit.fileUrl, "_blank")
						break
					case 2:
						if (!this.formData.attachmentStatement) {
							this.$dt.showMessage('您还未上传附件','warning')
							return
						}
						window.open(this.formData.attachmentStatement.fileUrl, "_blank")
						break
				}
			},
	  		validateProjectName () {
	  			if(!this.formData.projectName){
	  				this.$dt.showMessage('请先填写项目名称','warning')
	  				return false
	  			}

	  			return true
	  		},
	  		// 删除附件
	  		handleDelete () {

			},
			handleFillBudget () {
				this.$router.push({
					name: 'innovateForerunner',
					query:{
						id: this.formData.id || ''
					}
				})
			}
		},
		computed:{
			...mapState([
				'GlobalLoadingFlag'
			]),
			totalOutlay () {
				return Number(this.formData.fundSourceProject) + Number(this.formData.fundSourceSelf) + Number(this.formData.fundSourceOther)
			},
			budgetMenuList() {
				let data = this.$store.state.BUDGET_SUBJECT
				if(data.length){
					data = JSON.parse(data[0].label)
					data.forEach((item)=>{
						item.amount = ''
					})
				}
				return  data
			}
		},
		watch:{
			'GlobalLoadingFlag': function (val) {
				if(val === false){
					this.$nextTick(()=>{
						this.dialogShow = true
					})
				}
			}
		}
	}
